@font-face {
  font-weight: 400;
  font-family: "PTMono";
  font-style: normal;
  src: url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-style: bold;
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  font-family: "PTMono", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  background-color: #1a2931;
  background-size: 1910px auto;
  counter-reset: section; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  position: relative;
  display: block;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-right: 0;
  background-size: 100%; }

.page-main {
  height: 400px; }
